/** Widget avec un cycle d'initialisation. */
export interface IEltInitable extends HTMLElement {

	/**
	 * Initialise le widget via les paramÃ¨tres de la mÃ©thode et/ou les attributs du widget.
	 * Seul le 1er appel Ã  cette mÃ©thode est pris en compte. Les appels suivants sur la mÃªme instance sont ignorÃ©s.
	 * Cette mÃ©thode est automatiquement appelÃ©e, sans paramÃ¨tre, lorsque l'Ã©lÃ©ment est insÃ©rÃ©
	 * dans le document via CustomElement.connectedCallback().
	 *
	 * IMPORTANT : pour initialiser le widget avec des paramÃ¨tres, l'appel doit Ãªtre rÃ©alisÃ© avant l'insertion du widget dans le document.
	 *
	 * Note : certaines mÃ©thodes du widget pourraient ne pas fonctionner correctement avant cette initialisation.
	 */
	initialize(init?: any): this

	/** Indique si le widget a Ã©tÃ© initialisÃ©. */
	readonly initialized: boolean

	/**
	 * CrÃ©ation ou enrichissement d'un objet d'init Ã  partir des attributs de l'Ã©lement destinÃ© Ã 
	 * alimenter par dÃ©faut initialize(init). Si l'implÃ©mentation utilise l'attribut
	 * pour mÃ©moriser le state ou la config correspondant, la propriÃ©tÃ© n'est pas renseignÃ©e
	 * dans l'objet init retournÃ©.
	 *
	 * @param init Objet dÃ©jÃ  construit, Ã©ventuellement prÃ©-renseignÃ©, avec un reg par exemple.
	 * @return L'objet init enrichi ou instanciÃ© s'il n'Ã©tait pas spÃ©cifiÃ© en paramÃ¨tre.
	 */
	buildInitFromAtts(init?: any): any
}

/** Widget avec un cycle d'initialisation asynchrone. */
export interface IEltInitableAsync extends IEltInitable {

	/** Statut du widget vis Ã  vis de son cycle d'initilaisation. */
	initStatus: 'unInited' | 'pending' | 'inited' | 'initFailed'

	/** Promesse rÃ©solue (ou rejetÃ©e) lorsque le widget est initialisÃ© (ou en Ã©chec). */
	initializedAsync: Promise<this>
}

/** Widget gÃ©rant des Ã©tats et un processus de refresh. */
export interface IEltRefreshable extends HTMLElement {

	/** Enclenche le processus de refresh. */
	refresh(): void

	/**
	 * GÃ¨le ou dÃ©gÃ¨le l'Ã©lÃ©ment et ses sous-Ã©lements internes (dans son shadowRoot).
	 * Permet de bloquer les refresh pendant un batch.
	 *
	 * try {
	 *    widget.refreshFreeze(1);
	 *    ...
	 * } finally {
	 *    widget.refreshFreeze(-1);
	 *    widget.refresh();
	 * }
	 *
	 * @see BaseElement.refreshFreezeAll(elt, delta)
	 */
	refreshFreeze(delta: number): void
}

export function isEltRefreshable(elt: any): elt is IEltRefreshable {return elt && ('refreshFreeze' in elt)}


/** Config pour skin personnalisÃ© du widget. */
export interface OSkinableInit extends IRegPointer<any> {

	/** Registre Ã  utiliser pour rÃ©soudre les skins. */
	reg?: IReg<any>

	/** Code extPoints du skin principal personnalisÃ© Ã  installer. */
	skin?: string

	/** Codes extPoints sÃ©parÃ©s par des espaces de skins de surcharge du skin principal Ã  installer. */
	skinOver?: string
}

/**
 * Base pour tous les Ã©lÃ©ments IEltInitable et IEltStatable.
 *
 * Attributes :
 * Config attributes: skin, skin-over
 */
export class BaseElement extends HTMLElement implements CustomElement, IEltInitable, IEltRefreshable {
	/** GÃ¨le ou dÃ©gÃ¨le toute une descendance. */
	static refreshFreezeAll(elt: IEltRefreshable, delta: number) {
		elt.refreshFreeze(delta);
		for (let ch = DOM.findFirstChild(elt, isEltRefreshable); ch; ch = DOM.findNextSibling(ch, isEltRefreshable)) {
			BaseElement.refreshFreezeAll(ch, delta);
		}
	}

	protected _refreshFreeze = 1;

	get initialized(): boolean {return this._initialize === null}

	initialize(init?: any): this {
		if (this._initialize !== null) {
			this._initialize(init == null ? this.buildInitFromAtts() : init);
			this._initialize = null;
			this._refreshFreeze--;
		} else if (init) {
			console.warn("Widget already initialized", this, init);
		}
		return this;
	}

	refresh() {
		if (this._shouldRefresh()) this._refresh();
	}

	refreshFreeze(delta: number) {
		this._refreshFreeze += delta;
	}

	connectedCallback() {
		//on saute l'appel Ã  this.refresh() et this._shouldRefresh(): le refresh s'impose Ã  ce stade (sauf si _refreshFreeze>0).
		//Plus optimisÃ© pour les widgets agrÃ©geant d'autres widgets qui dispatchent le refresh sur les
		//sous-widgets dans la mÃ©thode refresh() et pas _refresh(), ce qui est recommandÃ©.
		this.initialize();
		if (this._refreshFreeze === 0) this._refresh();
	}

	/** Evalue si l'Ã©tat et la situation du widget justifie un appel Ã  _refresh()*/
	protected _shouldRefresh(): boolean {return this._refreshFreeze === 0 && this.isConnected}

	/** Execute un refresh du widget en prenant en compte le nouvel Ã©tat du widget. */
	protected _refresh() {}

	/**
	 * Initialisation de l'Ã©tat et configuration du widget.
	 * MÃ©thode appelÃ©e une seule fois dans la vie du widget.
	 */
	protected _initialize(init: any): void {}

	/**
	 * CrÃ©ation ou enrichissement d'un objet d'init Ã  partir des attributs de l'Ã©lement destinÃ© Ã 
	 * alimenter par dÃ©faut _initialize(init). Si l'implÃ©mentation utilise l'attribut
	 * pour mÃ©moriser le state ou la config correspondant, la propriÃ©tÃ© n'est pas renseignÃ©e
	 * dans l'objet init retournÃ©.
	 *
	 * @param init Objet dÃ©jÃ  construit, Ã©ventuellement prÃ©-renseignÃ©, avec un reg par exemple.
	 */
	buildInitFromAtts(init?: OSkinableInit): any {
		if (!init) init = {};
		if (this.hasAttribute('skin')) init.skin = BASIS.extractAttr(this, 'skin');
		if (this.hasAttribute('skinOver')) init.skinOver = BASIS.extractAttr(this, 'skinOver');
		return init;
	}

	/** Installe le skin du widget, Ã©ventuellement personnalisÃ© par la config. */
	_initAndInstallSkin(defaultSkin: string | null, init: OSkinableInit | null) {
		const skin = init?.skin || defaultSkin;
		const skinOver = init?.skinOver;
		if (skin || skinOver) {
			const reg = this.findReg(init);
			if (skin) reg.installSkin(skin, this.shadowRoot);
			if (skinOver) {
				for (const sk of skinOver.split(' ')) reg.installSkin(sk, this.shadowRoot);
			}
		}
	}

	/** MÃ©thode utilitaire pour attacher un shadow et dÃ©finir le skin et des enfants dessus */
	_attach(defaultSkin: string | null, init: OSkinableInit | null, ...children: Node[]): ShadowRoot {
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(defaultSkin, init);
		DOM.append(sr, ...children);
		return sr;
	}

	findReg<ENV>(init: IRegPointer<ENV>): IReg<ENV> {
		return REG.findReg(this, init);
	}

	_JsxProps: any;
}

/**
 * Base pour tous les Ã©lÃ©ments IEltInitableAsync et IEltStatable.
 */
export class BaseElementAsync extends BaseElement implements IEltInitableAsync {

	initStatus: 'unInited' | 'pending' | 'inited' | 'initFailed' = 'unInited';

	initializedAsync = new Promise<this>((resolve, reject) => {
		this._initResolved = resolve;
		this._initRejected = reject;
	});
	protected _initResolved: (value: this) => void;
	protected _initRejected: (reason?: any) => void;

	get initialized(): boolean {return this.initStatus === 'inited'}

	initialize(init: any = this.buildInitFromAtts()): this {
		if (this.initStatus === 'unInited') {
			this.initStatus = 'pending';
			this._initialize(init).then(() => {
				this.initStatus = 'inited';
				this._refreshFreeze--;
				const cb = this._initResolved;
				this._initResolved = null;
				this._initRejected = null;
				cb(this);
			}, (e) => {
				this.initStatus = 'initFailed';
				const cb = this._initRejected;
				this._initResolved = null;
				this._initRejected = null;
				cb(e);
				throw e;
			});
		}
		return this;
	}

	connectedCallback() {
		this.initialize();
		this.initializedAsync.then(() => {
			if (this._refreshFreeze === 0) this._refresh();
		});
	}

	/** A appeler si le widget est rÃ©initialisÃ©. */
	reinitializeStarted(promise: Promise<void>) {
		this.initStatus = 'pending';
		this.initializedAsync = promise.then(
			() => {
				this.initStatus = 'inited';
				return this
			},
			(e) => {
				this.initStatus = 'initFailed';
				throw e;
			}
		);
	}

	protected _initialize(init: any): Promise<void> {throw Error("not implemented")};
}

export function isEltInitableAsync(elt: any): elt is IEltInitableAsync {return elt && elt.initializedAsync}

export function isEltInitableAsyncPending(elt: any): elt is IEltInitableAsync {return elt && elt.initializedAsync && (elt.initStatus === 'unInited' || elt.initStatus === 'pending')}

type Writable<T> = { -readonly [P in keyof T]: T[P] };
export type PickInit<T, K extends keyof T> = Partial<Writable<Pick<T, K>>>;

