/**
 * Widget affichant un label et/ou une icone.
 *
 * Attributes : cf BaseElement +
 * State live attributes:      hidden
 * State init attributes:      label, icon
 * Classes:                    vertical
 */

export interface OLabelInit extends OSkinableInit {
	label?: string
	icon?: string
	title?: string
	hidden?: boolean
	/**
	 * Inclut l'icone et le span du label en fils direct (pas en shadowDom).
	 * Facilite le stylage externe mais dÃ©sactive tout stylage standard.
	 * Les propriÃ©tÃ©s skin et skinOver ne sont alors pas utilisÃ©es.
	 */
	noShadow?: boolean;

	/**
	 * Ajoute les propriÃ©tÃ©s pour gÃ©rer l'ellipse du contenu. Si noShadow est Ã  true,
	 * c'est au parent de les ajouter.
	 * Si aucun title n'est dÃ©fini dans l'init, un title basÃ© sur le contenu textuel
	 * du label sera ajoutÃ© / enlever en fonction de la prÃ©sence de l'ellipse.
	 */
	ellipsis?: boolean;
}

export interface Label extends BaseElement {
	initialize(init?: OLabelInit): this;
}

export class Label extends BaseElement {
	protected _label: string;
	protected _icon: string;

	get icon(): string {return this._icon}

	set icon(value: string) {
		if (this._icon != value) {
			this._icon = value;
			this.refresh();
		}
	}

	get label(): string {return this._label}

	set label(val: string) {
		if (this._label != val) {
			this._label = val;
			this.refresh();
		}
	}

	get hidden() {return this.hasAttribute('hidden')}

	set hidden(val: boolean) {
		if (DOM.setHidden(this, val)) this.refresh();
	}

	_iconElt: HTMLElement;
	_labelElt: HTMLElement;

	protected _initialize(init: OLabelInit) {
		if ('icon' in init) this._icon = init.icon;
		if ('label' in init) this._label = init.label;
		if ('title' in init) this.title = init.title;
		if ('hidden' in init) this.hidden = init.hidden;
		if ('ellipsis' in init) DOM.setAttrBool(this, 'ellipsis', init.ellipsis);
		if (!init.noShadow) {
			const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
			this._initSkin(init);
			sr.appendChild(document.createElement('slot'));
		}
	}

	buildInitFromAtts(init?: OLabelInit): OLabelInit {
		init = super.buildInitFromAtts(init);
		if (this.hasAttribute('label')) init.label = BASIS.extractAttr(this, 'label');
		if (this.hasAttribute('icon')) init.icon = BASIS.extractAttr(this, 'icon');
		return init;
	}

	protected _initSkin(init: OSkinableInit) {
		this._initAndInstallSkin(this.localName, init);
	}

	protected _refresh() {
		if (this.hidden) return;

		const icon = this.icon;
		if (icon) {
			if (!this._iconElt) this._createIcon();
			if (this._lastUrlIcon !== icon) {
				//NOTE : DOM.setStyle() avec une url ne marche pas Ã  cause des escapings.
				this._lastUrlIcon = icon;
				this.style.setProperty('--icon-img', icon.startsWith('--') ? `var(${icon})` : `url("${icon}")`);
			}
			DOM.setHidden(this._iconElt, false);
		} else {
			if (this._iconElt) DOM.setHidden(this._iconElt, true);
		}

		const label = this.label;
		if (label != null) {
			if (!this._labelElt) this._createLabel();
			DOM.setTextContent(this._labelElt, label);
			DOM.setHidden(this._labelElt, false);
		} else {
			if (this._labelElt) DOM.setHidden(this._labelElt, true);
		}
	}

	protected _lastUrlIcon: string;

	/** crÃ©taion de l'icon Ã  la demande. */
	_createIcon() {
		this._iconElt = (this.shadowRoot || this).insertBefore(<span class='icon'/>, this._labelElt);
	}

	/** crÃ©taion du label Ã  la demande. */
	_createLabel() {
		this._labelElt = (this.shadowRoot || this).insertBefore(<span class='label'/>, this._iconElt ? this._iconElt.nextSibling : null);
	}

	connectedCallback() {
		super.connectedCallback();
		if (this.hasAttribute('ellipsis') && !this.title) BASIS.titleOnEllipsisObserver.observe(this);
	}

	disconnectedCallback() {
		if (this.hasAttribute('ellipsis')) BASIS.titleOnEllipsisObserver.unobserve(this);
	}
}

customElements.define('c-label', Label);

