/**
 * Widget c-filesystem-path :affichage d'un path FS, avec btns d'action (ouverture, rÃ©vÃ©ler)
 *
 * Attributs : cf Label +
 * - type : 'dir' par dÃ©faut
 * - openBtn : false par dÃ©faut. SSi type='file'. Visible QUE dans le contexte "electron"
 * - revealBtn : true par dÃ©faut. Visible QUE dans le contexte "electron"
 *
 * Class :
 * - small
 */

export interface OFileSystemPathInit extends OLabelInit {
	openBtn?: boolean
	revealBtn?: boolean
	type?: 'file' | 'dir'
}

export interface FileSystemPath extends Label {
	initialize(init?: OFileSystemPathInit): this;
}

export class FileSystemPath extends Label {

	protected _openBtnElt: Button;

	protected _revealBtnElt: Button;

	_initialize(init: OFileSystemPathInit) {
		super._initialize(init);
		const reg = this.findReg(init);
		this.openBtn = ("openBtn" in init) ? init.openBtn : false;
		this.revealBtn = ("revealBtn" in init) ? init.revealBtn : true;
		this.type = init.type || 'dir';
		this.addEventListener("click", (event) => {
			if (this._labelElt)
				window.getSelection().selectAllChildren(this._labelElt);
		})
	}

	protected _initSkin(init: OSkinableInit) {
		const reg = this.findReg(init);
		reg.installSkin('c-label', this.shadowRoot);
		super._initSkin(init);
	}

	/** utile pour titleOnEllipsisObserver */
	get textContent(): string {
		return this._labelElt.textContent;
	}

	get openBtn(): boolean {return this.hasAttribute("openBtn")}

	set openBtn(md: boolean) {
		DOM.setAttrBool(this, "openBtn", md)
	}

	get revealBtn(): boolean {return this.hasAttribute("revealBtn")}

	set revealBtn(md: boolean) {
		DOM.setAttrBool(this, "revealBtn", md)
	}

	get type(): 'file' | 'dir' {return this.getAttribute("type") as any}

	set type(lv: 'file' | 'dir') {
		if (lv)
			this.setAttribute("type", lv);
		else
			this.removeAttribute("type");
	}

	buildInitFromAtts(init?: OFileSystemPathInit): OMsgLabelInit {
		init = super.buildInitFromAtts(init);
		if (this.hasAttribute('openBtn')) init.openBtn = true;
		if (this.hasAttribute('revealBtn')) init.revealBtn = true;
		if (this.hasAttribute('type')) init.type = BASIS.extractAttr(this, 'type') as any;
		return init;
	}

	protected _refresh() {
		super._refresh();
		const parentElt = this.shadowRoot || this;
		if (Desk.electron && this.openBtn && !this._openBtnElt)
			this._openBtnElt = parentElt.insertBefore(<c-button class="openBtn" Ã®={{title: "Ouvrir...", icon: "/@skin@/commons/icons/open.svg", role: "spinbutton"} as OButtonActionsInit<any>}
																													onclick={this.doOpen}/>, DOM.findLastChild(parentElt)) as Button;
		if (Desk.electron && this.revealBtn && !this._revealBtnElt)
			this._revealBtnElt = parentElt.insertBefore(<c-button class="revealBtn" Ã®={{title: "RÃ©vÃ©ler...", icon: "/@skin@/commons/icons/reveal.svg", role: "spinbutton"} as OButtonActionsInit<any>}
																														onclick={this.doReveal}/>, DOM.findLastChild(parentElt)) as Button;
	}

	/** RÃ©vÃ©ler le path */
	protected doReveal(ev: Event): void {
		window.postMessage({type: "client:modules:localServer:reveal", path: DOMSH.findHost<FileSystemPath>(this).textContent}, location.origin)
		ev.stopPropagation();
	}

	/** Ouvrir le path */
	protected doOpen(ev: Event): void {
		window.postMessage({type: "client:modules:localServer:open", path: DOMSH.findHost<FileSystemPath>(this).textContent}, location.origin);
		ev.stopPropagation();
	}

	/**
	 * Si msg==null, c-filesystem-path passÃ© en display:none.
	 * Si msg==="", c-filesystem-path vide mais pas hidden
	 */
	setPath(msg: string | null, type: 'file' | 'dir' | null, openBtn?: boolean | null, revealBtn?: boolean | null, icon?: string): this {
		if (msg == null) {
			this.hidden = true;
		} else {
			try {
				this.refreshFreeze(1);
				this.label = msg;
				if (icon !== null) this.icon = icon;
				if (type !== null) this.type = type;
				if (openBtn !== null) this.openBtn = openBtn;
				if (revealBtn !== null) this.revealBtn = revealBtn;
				if (this._openBtnElt)
					this._openBtnElt.disabled = msg === "";
				if (this._revealBtnElt)
					this._openBtnElt.disabled = msg === "";
				this.hidden = false;
			} finally {
				this.refreshFreeze(-1);
				this.refresh();
			}
		}
		return this;
	}

}

