/**
 * Widget c-msg
 *
 * Attributs : cf Label +
 * - level
 * - standard-msg
 *
 * Classes :
 * - headband : affichage sous forme de bandeau.
 */

export interface OMsgLabelInit extends OLabelInit {
	level?: 'info' | 'error' | 'warning' | 'valid' | null
	standardMsg?: 'loading' | 'saving' | 'deleting' | 'needAuth' | 'accessDenied' | 'netError' | null
}

export interface MsgLabel extends Label {
	initialize(init?: OMsgLabelInit): this;
}

export class MsgLabel extends Label {

	_initialize(init: OMsgLabelInit) {
		if (init.level) this.level = init.level;
		if (init.standardMsg) this.setStandardMsg(init.standardMsg);
		super._initialize(init);
	}

	buildInitFromAtts(init?: OMsgLabelInit): OMsgLabelInit {
		init = super.buildInitFromAtts(init);
		if (this.hasAttribute('standard-msg')) init.standardMsg = BASIS.extractAttr(this, 'standard-msg') as any;
		return init;
	}

	get level(): 'info' | 'error' | 'warning' | 'valid' | null {return this.getAttribute("level") as any}

	set level(lv: 'info' | 'error' | 'warning' | 'valid' | null) {
		if (lv)
			this.setAttribute("level", lv);
		else
			this.removeAttribute("level");
	}

	setStandardMsg(msg: 'loading' | 'saving' | 'deleting' | 'needAuth' | 'accessDenied' | 'netError' | null): this {
		if (!msg) {
			this.hidden = true;
		} else {
			this.hidden = false;
			this.className = msg;
			switch (msg) {
			case 'loading':
				DOM.setAttr(this, "level", "info");
				this.label = "Chargement en cours...";
				break;
			case 'saving':
				DOM.setAttr(this, "level", "info");
				this.label = "Enregistrement en cours...";
				break;
			case 'deleting':
				DOM.setAttr(this, "level", "info");
				this.label = "Suppression en cours...";
				break;
			case 'needAuth':
				DOM.setAttr(this, "level", "info");
				this.label = "Connexion nÃ©cessaire";
				break;
			case 'accessDenied':
				DOM.setAttr(this, "level", "error");
				this.label = "Autorisation refusÃ©e";
				break;
			case 'netError':
				DOM.setAttr(this, "level", "error");
				this.label = "AccÃ¨s au serveur impossible";
				break;
			}
		}
		return this;
	}

	/**
	 * Si msg==null, c-msg passÃ© en display:none.
	 * Si msg==="", c-msg vide mais pas hidden
	 */
	setCustomMsg(msg: string | null, level?: 'info' | 'error' | 'warning' | 'valid' | null, icon?: string): this {
		if (msg == null) {
			this.hidden = true;
		} else {
			try {
				this.refreshFreeze(1);
				this.label = msg;
				this.icon = icon;
				this.level = level;
				this.hidden = false;
			} finally {
				this.refreshFreeze(-1);
				this.refresh();
			}
		}
		return this;
	}
}

