/**
 * Message qui se place par dessus d'un objet de rÃ©fÃ©rence dans le but d'en bloquer les intercations
 * et d'informer l'utilisateur via le message.
 */
export class MsgOver extends MsgLabel {
	over: HTMLElement;
	protected _wasStatic: boolean;

	protected _initSkin(init: OSkinableInit) {
		const reg = this.findReg(init);
		reg.installSkin('c-msg', this.shadowRoot);
		this._initAndInstallSkin(this.localName, init);
	}

	showMsgOver(node: HTMLElement): this {
		while (node && node instanceof HTMLIFrameElement || node instanceof HTMLImageElement || node instanceof HTMLMediaElement) {
			//Impossible d'accrocher un msgOver sur ces Ã©lÃ©ments
			//XXX A affiner... quid si display = 'contents', 'table'...
			node = DOMSH.getFlatParentElt(node) as HTMLElement;
		}
		if (this.over && this.over !== node) this.removeMsg();
		this.over = node;
		const st = window.getComputedStyle(node);
		if (st.position === "static") {
			node.style.position = "relative";
			this._wasStatic = true;
		} else {
			this._wasStatic = false;
		}
		(node.shadowRoot || node).appendChild(this);
		return this;
	}

	async waitFor<T>(cb: PromiseLike<T> | (() => PromiseLike<T>)): Promise<T> {
		try {
			return await (typeof cb === 'function' ? (cb as () => PromiseLike<any>)() : cb);
		} finally {
			this.removeMsg();
		}
	}

	removeMsg(): this {
		if (!this.over) return;
		if (this._wasStatic) this.over.style.position = "static";
		if (this.parentNode) this.remove();
		return this;
	}
}

