/**
 * Widget de base: focusable et clickable affichant un label et/ou une icone.
 * Usages: bouton, checkbox, radio...
 *
 * Attributes : cf Label +
 * State live attributes: disabled
 * State init attributes: tabindex-custom
 */
export interface OClickableInit extends OLabelInit {
	disabled?: boolean
	tabIndexCustom?: number
}

export interface Clickable extends Label {
	initialize(init?: OClickableInit, ...args: any[]): this;
}

/** Widgets susceptibles de dÃ©clencher une action comme un bouton, un checkbox, un radiobutton... */
export abstract class Clickable extends Label {
	_tabIndexCustom: number;

	get disabled(): boolean {return this.hasAttribute('disabled')}

	set disabled(val: boolean) {
		if (DOM.setAttrBool(this, 'disabled', val)) this.refresh();
	}

	/**
	 * 0 par dÃ©faut.
	 * -1 pour empÃªcher que ce 'clickable', mÃªme enabled, soit accessible par navigation (tab).
	 * -2 pour empÃªcher que ce 'clickable', mÃªme enabled, soit focusable (revient Ã  suppr l'attribut tabindex)
	 */
	get tabIndexCustom(): number {return this._tabIndexCustom}

	set tabIndexCustom(val: number) {
		if (this._tabIndexCustom !== val) {
			this._tabIndexCustom = val;
			this.refresh();
		}
	}

	protected _initialize(init: OClickableInit) {
		BASIS.makeClickable(this);
		if (init.disabled) DOM.setAttrBool(this, 'disabled', true);
		this._tabIndexCustom = init.tabIndexCustom || 0;
		super._initialize(init);
	}

	buildInitFromAtts(init?: OClickableInit): OClickableInit {
		init = super.buildInitFromAtts(init);
		init.tabIndexCustom = '_tabIndexCustom' in this ? this._tabIndexCustom : BASIS.extractAttrAsNumber(this, 'tabindex-custom', 0);
		return init;
	}

	protected _refresh() {
		super._refresh();
		this.tabIndex = this.disabled ? -1 : this.tabIndexCustom;
	}

}

