export namespace BASIS {

	/** Rend un Ã©lÃ©ment focusable et exÃ©cutable. */
	export function makeClickable(elt: HTMLElement) {
		elt.tabIndex = 0;
		elt.addEventListener('keypress', function (ev: KeyboardEvent) {
			if (LANG.in(ev.key, 'Enter', ' ') && !ACTION.isAnyControlPressed(ev)) {
				ev.stopImmediatePropagation();
				ev.preventDefault();
				this.click();
			}
		});
	}

	export function extractAttr(node: Element, name: string): string {
		return node.getAttribute(name);
		//let val = node.getAttribute(name);
		//if (val != null) node.removeAttribute(name);
		//return val;
	}

	export function extractAttrAsNumber(node: Element, name: string, defaultVal: number): number {
		if (node.hasAttribute(name)) return Number.parseInt(extractAttr(node, name)) || defaultVal;
		return defaultVal;
	}

	export function refreshTree(from: Element | Document | DocumentFragment, excludeFrom?: boolean) {
		const tw = document.createTreeWalker(from, NodeFilter.SHOW_ELEMENT);
		if (excludeFrom && !tw.nextNode()) return;
		do {
			if (isEltRefreshable(tw.currentNode)) tw.currentNode.refresh();
		} while (tw.nextNode());
	}

	/**
	 * Efface tous les fils sauf les balises <style>.
	 * @see VIEWS.clearContent()
	 */
	export function clearContent(sr: ShadowRoot) {
		let ch = sr.lastChild;
		while (ch) {
			if (ch.localName !== "style") {
				const prev = ch.previousSibling as ChildNode;
				sr.removeChild(ch);
				ch = prev;
			} else {
				ch = ch.previousSibling as ChildNode;
			}
		}
	}

	/**
	 * Instancie un nouvel objet init en s'appuyant sur un Ã©ventuel objet existant en prototype
	 * et en associant un registre.
	 */
	export function newInit<I extends IRegPointer<any>>(from: I, reg: IReg<any>): I {
		const i = Object.create(from || null);
		i.reg = reg;
		return i;
	}

	/**
	 * ResizeObserver s'occupant de dÃ©finir un title basÃ© sur le contenu en fonction
	 * de l'Ã©tat d'overflow, utile quand un ellipsis est dÃ©fini sur l'Ã©lement.
	 */
	export const titleOnEllipsisObserver = new ResizeObserver((entries) => {
		for (const entry of entries) {
			const target = entry.target as HTMLElement;
			if (target.scrollWidth > Math.ceil(entry.contentRect.width) || target.scrollHeight > Math.ceil(entry.contentRect.height) /* check height aussi pour ellipsis avec css line-clamp */) {
				target.title = entry.target.textContent;
			} else {
				target.title = "";
			}
		}
	});

	/**
	 * Rend visible un elt dans un scrollable container en gÃ©rant le scroll smooth si "raisonnable"
	 * (contournement imparfait de l'instabilitÃ© de chromium sur le scroll smooth).
	 */
	export function ensureVisible(elt: HTMLElement, scrollCtn: HTMLElement | null, blockPos: 'nearest' | 'start' = 'start') {
		if (scrollCtn) {
			if (getComputedStyle(scrollCtn).scrollBehavior === 'smooth') {
				const target = elt.getBoundingClientRect();
				const scrollBox = scrollCtn.getBoundingClientRect();
				let delta: number;
				if (blockPos === 'nearest') {
					delta = Math.min(Math.abs(target.bottom - scrollBox.top), Math.abs(target.top - scrollBox.bottom));
				} else {
					delta = Math.abs(target.top - scrollBox.top);
				}
				if (delta > scrollBox.height * 2) {
					//console.log("box scrollBehavior:::instant");
					scrollCtn.style.scrollBehavior = 'auto';
					elt.scrollIntoView({block: blockPos, behavior: 'auto'});
					scrollCtn.style.scrollBehavior = 'smooth';
				} else {
					//console.log("box scrollBehavior:::smooth");
					elt.scrollIntoView({block: blockPos, behavior: 'smooth'});
				}
			} else {
				//smooth dÃ©sactivÃ©.
				elt.scrollIntoView({block: blockPos});
			}
		} else {
			elt.scrollIntoView({block: blockPos, behavior: 'smooth'});
		}
	}

	export const DEBUG = false;

}
